import React, { useState, useEffect, useContext } from 'react';
import { HealthContext } from '../../context/healthContext.js';

const Create = props => {
  const [name, setName] = useState('');
  const [imageURL, setImageURL] = useState('');
  const { postRecipe } = props;

  function handleChange(e) {
    const name = e.target.name;
    const value = e.target.value;

    setName(value);
  }

  function uploadImage() {
    console.log('upload image');
    setImageURL('https://futuredish.com/wp-content/uploads/2018/01/Mapo-Tofu-1.jpg');
  }

  function handleSubmit(e) {
    e.preventDefault();
    const newRecipe = {};
    newRecipe.name = name;
    newRecipe.imageURL = imageURL;
    newRecipe.like = 180;
    newRecipe.ingredients = ["100g tofu", "1g pepper"];
    newRecipe.methods = [{description: "cook tofu in hot water"},{description: "fry the tofu in a pan"}];
    newRecipe.user = 'Jack Ross';
    newRecipe.userURL = 'https://www.the-happy-chef.com/assets/img/team/team-3.jpg';
    postRecipe(newRecipe);
    /*
    data {
      name: 'Mapo Tofu',
      imageURL: '',
      like: '',
      ingredients: '',
      methods: '',
      user: '',
      userURL: ''
    }
    */
  }

  return (
    <div className="create">

        <div className="create-editor">
          <h2>Add Your Recipe</h2>

          <form onSubmit={handleSubmit}>

            <h4>Step 1: Add recipe name</h4>
            <input className="create-input"
              name="name"
              type="text"
              vaule={name}
              onChange={handleChange}
              placeholder="Recipe Name"></input>

            <h4>Step 2: Add the finished image</h4>
            <input type="file"
            id="input"
            ref={React.createRef}
            name="image"
            accept="image/*"
            onChange={uploadImage}
            />

            <button className="create-submit-button"
              type="submit">
              Save recipe
            </button>

          </form>
        </div>
      </div>
  )
}

export default Create;
